/* Composant Bouton Billeterie */

  .btn {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    font-size: 18px;
    color: #030303;
    cursor: pointer;
    background-color: rgb(255, 255, 255);
  }
  
  .btn:hover {
    padding: 6px 18px;
    transition: all 0.3s ease-out;
    background-color: transparent;
    color: #fff;
    border-radius: 4px;
    border: 2px solid cadetblue;
    transition: all 0.3s ease-out;
  }

