/* Composant Menu Déroulant 2 Dropdownpart*/


.dropdown-menu {
  width: 20vh;
  position: absolute;
  top: 85px;
  list-style: none;
  text-align: start;
  animation: dropdownAnimation 0.5s ease; /* Apply animation */
  margin:auto;
  }

  @keyframes dropdownAnimation {
    from {
      opacity: 0;
      transform: translateY(-30px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .dropdown-menu li {
    background-color: cadetblue;
    cursor: pointer;
    text-align: center;
    padding: 12px;
    margin-top: -5px;
    color: white!important;
    font-size: 15px;

  }
  
  .dropdown-menu li:hover {
    background-color: rgb(255, 255, 255);
    text-decoration: none;
   
  }
  
  .dropdown-menu.clicked {
    display: none;
  }
  
  .dropdown-link {
    display: block;
    height: 100%;
    width: 100%;
    text-decoration: none;
    color: rgb(0, 0, 0);
    font-weight: bold;
    margin-left: -15px;

  }
  
  /* Style pour la mise en page responsive */
  @media screen and (max-width: 1024px) {
  /* largeur de l'écran est inférieure ou égale à 1024px */
  
    .fa-caret-down {
      display: none;
    }



  }